@import url('https://fonts.googleapis.com/css2?family=Space+Grotesk:wght@300..700&display=swap');

* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}
:root {
  --Mobile: 375px;
  --Desktop: 1440px;
  --Linear-gradient-active-inputborder-1: hsl(249, 99%, 64%); 
  --Linear-gradient-active-inputborder-1: hsl(278, 94%, 30%);
  --Red-input-errors: hsl(0, 100%, 66%);
  --White: hsl(0, 0%, 100%);
  --Light-grayish-violet: hsl(270, 3%, 87%);
  --Dark-grayish-violet: hsl(279, 6%, 55%);
  --Very-dark-violet: hsl(278, 68%, 11%);
  --Font-size: 18px;
}

body {
  margin: 0;
  font-family: "Space Grotesk", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
#root {
  display: flex;
  flex-direction: column;
  align-items: center;
}
body, #root {
  width: 100vw;
  height: 100vh;
  background-color: var(--Light-grayish-violet);
}
main {
  display: flex;
  flex-direction: column;
  width: var(--Mobile);
  background-color: var(--White);
}
.display {
  background-image: url(interactive-card-details-form-main/images/bg-main-mobile.png);
  position: relative;
  height: 200px;
}
.card-backview {
  position: absolute;
  background-color: var(--Light-grayish-violet);
  width: 270px;
  height: 120px;
  border-radius: 5px;
  display: flex;
  flex-direction: column;
  align-items: center;
  top: 35px;
  left: 75px;
}
.card-frontview {
  position: absolute;
  background-image: url(interactive-card-details-form-main/images/bg-card-front.png);
  background-size: cover;
  background-repeat: no-repeat;
  width: 270px;
  top: 106px;
  left: 20px;
  border-radius: 5px;
  padding: 10px;
}
.blackline {
  background-color: var(--Very-dark-violet);
  height: 25px;
  width: 100%;
  margin-top: 10px;
}
.cvc {
  background-color: var(--Dark-grayish-violet);
  width: 230px;
  padding: 3px;
  text-align: right;
  color: var(--White);
  margin-top: 10px;
  border-radius: 5px;
  padding-right: 9px;
}
.logo img {
  width: 50px;
  margin-bottom: 20px;
}
.card-number p {
  color: var(--White);
  letter-spacing: 3px;
  word-spacing: 4px;
  margin-bottom: 10px;
}
.card-name {
  display: flex;
  justify-content: space-between;
  margin-bottom: 10px;
}
.card-name .name {
  color: var(--White);
  font-size: x-small;
}
.card-name .expiry-date {
  color: var(--White);
  font-size: x-small;
}
form {
  margin: 60px 15px 10px;
}
input {
  appearance: none;
  border: 1px solid var(--Light-grayish-violet);
  margin: 3px 0;
}
.field {
  margin: 15px 0;
}
label, .exp {
  font-size: small;
  font-weight: 700;
  margin-bottom: 5px;
}
input::placeholder {
  color: rgba(0, 0, 0, 0.4);
}
.valid {
  border-color: var(--Dark-grayish-violet);
}
.invalid {
  border-color: var(--Red-input-errors);
  outline: var(--Red-input-errors);
}
.input {
  border-color: var(--Dark-grayish-violet);
}
#card-name, #card-number {
  display: block;
  width: 100%;
  padding: 10px;
  border-radius: 5px;
}
.date {
  display: flex;
  gap: 20px;

  margin-top: 10px;
}
.date input {
  width: 60px;
  display: block;
  padding: 7px;
  border-radius: 5px;
}
#cvc {
  width: 100%;
  padding: 7px;
  margin-top: 5px;
}
.date-input {
  display: flex;
  gap: 10px;
  margin-top: 5px;
}
.exp, .exp-date {
  vertical-align: middle;
  line-height: 1;
}
#submit {
  width: 100%;
  padding: 15px 0;
  border-radius: 5px;
  background-color: var(--Very-dark-violet);
  color: var(--Light-grayish-violet);
  margin-top: 10px;
  border: 0;
  margin-bottom: 20px;
}
#submit:hover {
  background-color: var(--Light-grayish-violet);
  color: var(--Very-dark-violet);
}
.error {
  color: var(--Red-input-errors);
  font-size: x-small;
}
.success {
  margin-top: 70px;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 0 15px;
}
.success-img img {
  width: 60px;
}
.success-text h1 {
  font-weight: 500;
  text-align: center;
  margin: 10px 0;
}
.success-text p {
  text-align: center;
  margin: 10px 0 30px;
  color: rgba(0, 0, 0, 0.5);
}
.continue-btn {
  width: 100%;
  padding: 15px;
  border-radius: 5px;
  border: 0;
  color: var(--Light-grayish-violet);
  background-color: var(--Very-dark-violet);
  margin: 0 10px 40px;
  letter-spacing: 1px;
}
.continue-btn:hover {
  background-color: var(--Light-grayish-violet);
  color: var(--Very-dark-violet);
}
footer p {
  text-align: center;
}
/* Define CSS animations */
@keyframes fadeout {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

/* Apply animations to components */
.success-animation {
  animation: fadeout 1s ease-in-out forwards;
}

@media only screen and (min-width: 750px) {
  #root {
    justify-content: center;
  }
  main {
    min-height: 100vh;
    width: 650px;
    position: relative;
    margin-bottom: 20px;
  }
  .display {
    background-image: url(interactive-card-details-form-main/images/bg-main-desktop.png);
    background-repeat: no-repeat;
    min-height: 100vh;
    width: 300px;
  }
  form {
    position: absolute;
    left: 55%;
    top: 170px;
  }
  .card-backview {
    width: 270px;
    height: 120px;
    top: 410px;
    left: 75px;
  }
  .card-frontview {
    width: 270px;
    top: 255px;
    left: 70px;
  }
  .blackline {
    height: 25px;
    width: 100%;
  }
  .cvc {
    margin-top: 10px;
    width: 200px;
  }
  .logo img {
    width: 70px;
    margin-bottom: 20px;
    margin-left: 10px;
  }
  .card-number p {
    color: var(--White);
    letter-spacing: 3px;
    word-spacing: 4px;
    margin-bottom: 10px;
  }
  .card-name .name {
    font-size: xx-small;
  }
  .card-name .expiry-date {
    font-size: xx-small;
    margin-right: 15px;
  }
  .copyright {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 10px;
  }
  .dot, .short, .semi, .long {
    background-color: var(--Dark-grayish-violet);
    height: 5px;
    border-radius: 10px;
  }
  .one, .two, .three {
    display: flex;
    gap: 5px;
    margin-bottom: 3px;
  }
  .dot {
    width: 10px;
  }
  .short {
    width: 20px;
  }
  .semi {
    width: 30px;
  }
  .long {
    width: 60px;
  }
}


@media only screen and (min-width: 1400px) {
  #root {
    justify-content: center;
  }
  main {
    min-height: 100vh;
    width: var(--Desktop);
    position: relative;
    margin-bottom: 20px;
  }
  .display {
    background-image: url(interactive-card-details-form-main/images/bg-main-desktop.png);
    background-repeat: no-repeat;
    background-size: cover;
    width: 500px;
    min-height: 100vh;
  }
  form {
    position: absolute;
    left: 55%;
    top: 170px;
  }
  .card-backview {
    width: 330px;
    height: 180px;
    top: 450px;
    left: 310px;
  }
  .card-frontview {
    width: 330px;
    height: 180px;
    top: 240px;
    left: 260px;
  }
  .blackline {
    height: 45px;
    width: 100%;
    margin-top: 15px;
  }
  .cvc {
    margin-top: 10px;
    width: 300px;
    padding: 7px 10px;
  }
  .logo img {
    width: 100px;
    margin-bottom: 20px;
    margin-left: 10px;
  }
  .card-number p {
    color: var(--White);
    letter-spacing: 6px;
    word-spacing: 4px;
    margin-bottom: 20px;
  }
  .card-name .name {
    font-size: small;
  }
  .card-name .expiry-date {
    font-size: small;
    margin-right: 15px;
  }
  .copyright {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 10px;
  }
  .dot, .short, .semi, .long {
    background-color: var(--Dark-grayish-violet);
    height: 5px;
    border-radius: 10px;
  }
  .one, .two, .three {
    display: flex;
    gap: 5px;
    margin-bottom: 3px;
  }
  .dot {
    width: 10px;
  }
  .short {
    width: 20px;
  }
  .semi {
    width: 30px;
  }
  .long {
    width: 60px;
  }
}
